 margin-top: -80px;\r\n  z-index: -10;\r\n  width: 100%;\r\n  height: 88vh;\r\n  .container{\r\n    color: white;\r\n    object-fit: cover;\r\n    position: fixed;\r\n    z-index: -1;\r\n    width: 100%;\r\n    height: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n  }\r\n  .textContent{\r\n    // background-color: black;\r\n    z-index: -1;\r\n    color: white;\r\n    position: fixed;\r\n    top: 40%;\r\n    right: 8%;\r\n    text-align: end;\r\n    h1{\r\n      text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.314);\r\n      font-size: 6rem;\r\n      font-style: italic;\r\n      &:first-letter{\r\n        color: #116979;\r\n        font-size: 9rem;\r\n        text-shadow: 2px 2px 10px rgba(243, 243, 243, 0.579);\r\n      }\r\n    }\r\n    h2{\r\n      font-weight: 400;\r\n      font-size: 2.3rem;\r\n      text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.458);\r\n    }\r\n  }\r\n}\r\n.restaurantCatalog{\r\n  padding: 0 10em;\r\n  padding-top: 2em;\r\n  padding-bottom: 5em;\r\n  width: 100%;\r\n  height: auto;\r\n  background-color: $grey;\r\n\r\n  .subHeadingContainer{\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    form{\r\n      border: 3px solid $orange;\r\n      display: flex;\r\n      width: 250px;\r\n      height: 50px;\r\n      input{\r\n        height: 100%;\r\n        width: 80%;\r\n        outline: none;\r\n        border: none;\r\n        color: $darkGreen;\r\n        background-color: transparent;\r\n        font-weight: 500;\r\n        &::placeholder{\r\n          color: $darkGreen;\r\n          opacity: 0.6;\r\n        }\r\n      }\r\n      iconify-icon{\r\n        height: 100%;\r\n        width: 20%;\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n        font-size: 1.3rem;\r\n        color: $darkGreen;\r\n      }\r\n    }\r\n  }\r\n}\r\n// restaurantList\r\nrestaurant-list{\r\n  display: flex;\r\n  justify-content: space-around;\r\n  padding: 5px;\r\n  margin-top: 3em;\r\n  width: 100%;\r\n  height: auto;\r\n  gap: 10px;\r\n  row-gap: 50px;\r\n  flex-wrap: wrap;\r\n  .notFound{\r\n    color: $lightGreen;\r\n    font-weight: 500;\r\n    font-style: italic;\r\n  }\r\n  restaurant-item{\r\n    border-top-right-radius: 20px;\r\n    border-top-left-radius: 20px;\r\n    background-color: $white;\r\n    overflow: hidden;\r\n    width: 260px;\r\n    height: 420px;\r\n    box-shadow: 2px 4px 20px rgba(0, 0, 0, 0.15);\r\n    .imgArea{\r\n      position: relative;\r\n      width: 100%;\r\n      height: 45%;\r\n      background-color: rgba(0, 0, 0, 0.34);\r\n      overflow: hidden;\r\n      img{\r\n        height: 100%;\r\n      }\r\n      .restaurantLoc{\r\n        font-size: 1rem;\r\n        width: auto;\r\n        top: 10%;\r\n        padding: 5px 20px;\r\n        position: absolute;\r\n        background-color: \trgba(245,245,245,0.8);\r\n        border-top-right-radius: 6px;\r\n        border-bottom-right-radius: 6px;\r\n        color: $darkGreen;\r\n        font-weight: 700;\r\n      }\r\n    }\r\n    .descriptionArea{\r\n      height: 55%;\r\n      padding: 1em;\r\n      display: flex;\r\n      flex-direction: column;\r\n      .restaurantName{\r\n        text-align: center;\r\n        background-color: $lightGreen;\r\n        color: white;\r\n        padding: 0.3em 1em;\r\n        margin: 0 auto;\r\n        font-size: 1.3rem;\r\n        margin-top: -35px;\r\n        z-index: 1;\r\n        border-radius: 10px;\r\n        width: auto;\r\n      }\r\n      }\r\n      .description{\r\n        flex-grow: 0;\r\n        margin-top: 5px;\r\n        font-size: 1rem;\r\n        max-height: 50%;\r\n        font-weight: 400;\r\n        text-align: justify;\r\n        color: $lightGreen;\r\n        overflow: auto;\r\n      }\r\n      .detailsButton{\r\n        cursor: pointer;\r\n        flex: 1 0 44px;\r\n        margin-top: 10px;\r\n        outline: none;\r\n        border: 3px solid $darkGreen;\r\n        border-radius: 10px;\r\n        font-size: 0.9rem;\r\n        font-weight: 700;\r\n        color: $darkGreen;\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n        width: auto;\r\n        &:hover{\r\n          background-color: $darkGreen;\r\n          color: white;\r\n        }\r\n      }\r\n  }\r\n  }\r\nfooter{\r\n  width: 100%;\r\n  height: 60px;\r\n  background-color: $orange;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  color: white;\r\n  font-weight: 600;\r\n}\r\n// DETAIL\r\n#detailContent {\r\n  width: 100%;\r\n  padding: 0 10em;\r\n  padding-top: 4em;\r\n  overflow: hidden;\r\n  padding-bottom: 80px;\r\n  .detailHead{\r\n    display: flex;\r\n    .img{\r\n      width : 40%;\r\n      height: fit-content;\r\n      img{\r\n        border-radius: 20px;\r\n        width: 100%;\r\n      }\r\n      .detailButtonArea{\r\n        margin-top: 20px;\r\n        width: 100%;\r\n        height: 40px;\r\n        display: flex;\r\n        gap: 5px;\r\n        font-size: 0.9rem;\r\n        a,.likeOrLikedButton .addToFavorite{\r\n          height: 50px;\r\n          outline: none;\r\n          background-color: transparent;\r\n          border: 3px solid $darkGreen;\r\n          cursor: pointer;\r\n          font-weight: 600;\r\n          color: $darkGreen;\r\n          &:hover{\r\n            background-color: $darkGreen;\r\n            color: white;\r\n          }\r\n        }\r\n        a{\r\n          display: flex;\r\n          align-items: center;\r\n          justify-content: center;\r\n          width: 50%;\r\n          border-top-left-radius: 30px;\r\n          border-bottom-left-radius: 30px;\r\n          border-top-right-radius: 5px;\r\n          border-bottom-right-radius: 5px;\r\n          font-size: inherit;\r\n          color: $darkGreen\r\n        }\r\n        .likeOrLikedButton{\r\n          height: 50px;\r\n          width: 50%;\r\n          .addToFavorite{\r\n            height: 100%;\r\n            width: 100%;\r\n            font-size: inherit;\r\n            border-top-right-radius: 30px;\r\n            border-bottom-right-radius: 30px;\r\n            border-top-left-radius: 5px;\r\n            border-bottom-left-radius: 5px;\r\n          }\r\n          .removeFromFavorite{\r\n            width: 100%;\r\n            height: 100%;\r\n            outline: none;\r\n            background-color: $darkGreen;\r\n            border: none;\r\n            cursor: pointer;\r\n            font-weight: 600;\r\n            color: $darkGreen;\r\n            color: white;\r\n            &:hover{\r\n              background-color: $lightGreen;\r\n              color: white;\r\n            }\r\n            font-size: inherit;\r\n            border-top-right-radius: 30px;\r\n            border-bottom-right-radius: 30px;\r\n            border-top-left-radius: 5px;\r\n            border-bottom-left-radius: 5px;\r\n          }\r\n        }\r\n      }\r\n    }\r\n    .details{\r\n      padding-left: 30px;\r\n      flex: 1 1 70%;\r\n      overflow:auto;\r\n      .restaurantName {\r\n        display: inline;\r\n        color: $darkGreen;\r\n        text-shadow: 0px 1px rgba(110, 108, 108, 0.525);\r\n        width: 100px;\r\n        font-size: 2rem;\r\n      }\r\n      .starsArea{\r\n        margin: 5px 0;\r\n      }\r\n      h3{\r\n        font-size: 1.2rem;\r\n        font-weight: 500;\r\n      }\r\n      .restaurantLocation{\r\n        font-weight: 700;\r\n        color: $lightGreen;\r\n      }\r\n      .restaurantDesc{\r\n        color: $darkGreen;\r\n        margin-top: 10px;\r\n        text-align: justify;\r\n      }\r\n    }\r\n  }\r\n  .menuAndReviewContainer{\r\n    gap: 70px;\r\n    margin-top: 30px;\r\n    display: flex;\r\n    justify-content: space-between;\r\n  }\r\n  .menu{\r\n    display: flex;\r\n    justify-content: center;\r\n    gap: 4em;\r\n    h3{\r\n      color: $darkGreen;\r\n      margin-bottom: 15px;\r\n      font-size: 1.5rem;\r\n      text-align: center;\r\n    }\r\n    ul{\r\n      padding-left: 15px;          \r\n      margin-top: 5px;\r\n      li{\r\n        font-size: 1.2rem;\r\n        color: $darkGreen;\r\n        font-weight: 500;\r\n        list-style-position: inside;\r\n      }\r\n    }\r\n  }\r\n  .reviewContainer{\r\n    flex: 1 1 50%;\r\n    h2{\r\n      font-size: 1.5rem;\r\n      text-align: center;\r\n      color: $darkGreen;\r\n    }\r\n    review-list{\r\n      margin-top: 20px;\r\n      width: 100%;\r\n      height: 400px;\r\n      padding: 0 0.3em;\r\n      display: flex;\r\n      flex-direction: column;\r\n      gap: 10px;\r\n      overflow: scroll;\r\n      review-item{\r\n        padding: 1em 1em;\r\n        width: 100%;\r\n        border: 3px solid $orange;\r\n        border-radius: 5px;\r\n        .reviewHead{\r\n          font-size: 1rem;\r\n          font-weight: 500;\r\n          color: $lightGreen;\r\n          span{\r\n            font-weight: 300;\r\n            color: black;\r\n            font-size: .9rem;\r\n          }\r\n        }\r\n        .reviewText{\r\n          font-size: 1rem;\r\n          margin-top: 5px;\r\n          font-weight: 500;\r\n        }\r\n      }\r\n      form{\r\n        display: flex; \r\n        justify-content: space-between;\r\n        gap: 20px;\r\n        .inputArea{\r\n          display: flex;\r\n          flex-direction: column;\r\n          flex: 1;\r\n          gap: 10px;\r\n          input{\r\n            border: 2px solid $darkGreen;\r\n            padding: 10px 20px;\r\n            border-radius: 5px;\r\n            height: 49px;\r\n            padding-left: 1em;\r\n            color: $darkGreen;\r\n            font-weight: 600;\r\n            \r\n            &::placeholder{\r\n              color: $darkGreen;\r\n              font-weight: 400;\r\n            }\r\n          }\r\n          #reviewText{\r\n            height: 50px;\r\n          }\r\n        }\r\n        button{\r\n          cursor: pointer;\r\n          outline: none;\r\n          background-color: $darkGreen;\r\n          color: white;\r\n          font-weight: 600;\r\n          border: none;\r\n          width: 100px;\r\n          height: 49px;\r\n          border-radius: 5px;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n.closeButton {\r\n  position: absolute;\r\n  top: 15px;\r\n  right: 15px;\r\n  cursor: pointer;\r\n  width: 35px;\r\n  height: 35px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n\r\n  iconify-icon{\r\n    font-size: 2.5rem;\r\n    color: $orange;\r\n  }\r\n}\r\n\r\n// Favorites\r\n#favorites{\r\n  background-color: $grey;\r\n  padding: 3em 10em;\r\n  width: 100%;\r\n  min-height: 100vh;\r\n  .subHeading{\r\n    width: 100%;\r\n    display: block;\r\n    text-align: center;\r\n    font-size: 2.1rem;\r\n  }\r\n}\r\n// notFound\r\n.notFoundContainer{\r\n  // background-color: red;\r\n  width: 100%;\r\n  height: 90vh;\r\n  display: flex;\r\n  align-items: center;\r\n  // justify-content: center;\r\n  flex-direction: column;\r\n  margin-top: 8em;\r\n  \r\n  h2{\r\n    font-size: 16rem;\r\n    font-weight: 800;\r\n    color: $darkGreen;\r\n    text-shadow: 2px 2px 5px black;\r\n  }\r\n  h4{\r\n    font-size: 2rem;\r\n    color: $orange;\r\n  }\r\n}\r\n\r\n// OVERLAY\r\n.overlay{\r\n  width: 100%;\r\n  height: 100vh;\r\n  position: fixed;\r\n  top: 0;\r\n  right: 0;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: rgba(0, 0, 0, 0.278);\r\n}\r\n\r\n// LOADER\r\n.loader,\r\n.loader:before,\r\n.loader:after {\r\n  background: #ffffff;\r\n  -webkit-animation: load1 1s infinite ease-in-out;\r\n  animation: load1 1s infinite ease-in-out;\r\n  width: 1em;\r\n  height: 4em;\r\n}\r\n.loader {\r\n  color: #ffffff;\r\n  text-indent: -9999em;\r\n  margin: 88px auto;\r\n  position: relative;\r\n  font-size: 11px;\r\n  -webkit-transform: translateZ(0);\r\n  -ms-transform: translateZ(0);\r\n  transform: translateZ(0);\r\n  -webkit-animation-delay: -0.16s;\r\n  animation-delay: -0.16s;\r\n}\r\n.loader:before,\r\n.loader:after {\r\n  position: absolute;\r\n  top: 0;\r\n  content: '';\r\n}\r\n.loader:before {\r\n  left: -1.5em;\r\n  -webkit-animation-delay: -0.32s;\r\n  animation-delay: -0.32s;\r\n}\r\n.loader:after {\r\n  left: 1.5em;\r\n}\r\n@-webkit-keyframes load1 {\r\n  0%,\r\n  80%,\r\n  100% {\r\n    box-shadow: 0 0;\r\n    height: 4em;\r\n  }\r\n  40% {\r\n    box-shadow: 0 -2em;\r\n    height: 5em;\r\n  }\r\n}\r\n@keyframes load1 {\r\n  0%,\r\n  80%,\r\n  100% {\r\n    box-shadow: 0 0;\r\n    height: 4em;\r\n  }\r\n  40% {\r\n    box-shadow: 0 -2em;\r\n    height: 5em;\r\n  }\r\n}\r\n\r\n\r\n@keyframes showNavigation {\r\n  0%{\r\n    display: flex;\r\n  }\r\n  100%{\r\n    transform: translateY(80px);\r\n  }\r\n}\r\n\r\n\r\n"],"sourceRoot":""}]);
